import Nav from './Nav';

const Header = () => {
  return (
    <header>
      <div>Medical Malpractice</div>
      <Nav />

      <style jsx>{`
        header {
          display: flex;
          justify-content: space-between;
          flex-flow: wrap;
          background-color: #000000;
          color: #ffffff;
          align-items: center;
        }
      `}</style>
    </header>
  );
};

export default Header;
